import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { RxCommandFactoryService } from '@helix/platform/shared/api';
import { castArray } from 'lodash';

export const associateMultipleCommand = 'com.bmc.arsys.rx.application.association.command.AssociateMultipleCommand';
export const disassociateMultipleCommand =
  'com.bmc.arsys.rx.application.association.command.DisassociateMultipleCommand';

@Injectable({
  providedIn: 'root'
})
export class RxAssociationInstanceService {
  private associateCommand = this.rxCommandFactoryService.forResourceType(associateMultipleCommand);
  private disassociateCommand = this.rxCommandFactoryService.forResourceType(disassociateMultipleCommand);

  constructor(private rxCommandFactoryService: RxCommandFactoryService) {}

  associateRecords(
    associationDefinitionName: string,
    nodeAIds: string | string[],
    nodeBIds: string | string[],
    useDefaultRoles: boolean,
    nodeARole: string,
    nodeBRole: string
  ): Observable<any> {
    return this.associateCommand.execute({
      associationDefinitionName,
      nodeARecordInstanceIds: castArray(nodeAIds),
      nodeBRecordInstanceIds: castArray(nodeBIds),
      useDefaultRoles,
      nodeARole,
      nodeBRole
    });
  }

  disassociateRecords(
    associationDefinitionName: string,
    nodeAIds: string | string[],
    nodeBIds: string | string[]
  ): Observable<any> {
    return this.disassociateCommand.execute({
      associationDefinitionName,
      nodeARecordInstanceIds: castArray(nodeAIds),
      nodeBRecordInstanceIds: castArray(nodeBIds)
    });
  }
}
