import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ICommand, IHttpGetParams, IHttpOptions, RX_BUNDLE, RxCommandFactoryService } from '@helix/platform/shared/api';
import { Observable, of } from 'rxjs';
import { RX_ASSOCIATION_DEFINITION } from './association-definition.constant';
import { IAssociationDefinition } from './association-definition.types';

@Injectable({
  providedIn: 'root'
})
export class RxAssociationDefinitionService {
  constructor(private httpClient: HttpClient, private rxCommandFactoryService: RxCommandFactoryService) {}

  get(associationDefinitionName: string, options?: IHttpGetParams): Observable<IAssociationDefinition> {
    return this.httpClient.get<IAssociationDefinition>(this.getUrl(associationDefinitionName), options);
  }

  getNew(): Observable<IAssociationDefinition> {
    return of({
      name: '',
      description: null,
      nodeAId: null,
      nodeBId: null,
      nodeAKeys: [379],
      nodeBKeys: [379],
      nodeAName: null,
      nodeBName: null,
      cardinality: RX_ASSOCIATION_DEFINITION.cardinality.oneToOne.value,
      shouldCascadeDelete: false,
      nodeAModality: RX_ASSOCIATION_DEFINITION.modality.optional,
      isEnabled: true,
      allowOverlay: false,
      scope: RX_BUNDLE.definitionScopeTypes.bundle
    });
  }

  create(associationDefinition: IAssociationDefinition): Observable<HttpResponse<any>> {
    return this.httpClient.post(this.getUrl(), associationDefinition, { observe: 'response' });
  }

  update(associationDefinition: IAssociationDefinition, options?: IHttpOptions): Observable<any> {
    return this.httpClient.put(this.getUrl(associationDefinition.name), associationDefinition, options);
  }

  delete(associationDefinitionName: string): Observable<any> {
    return this.httpClient.delete(this.getUrl(associationDefinitionName));
  }

  revertCustomization(associationDefinitionName: string): Observable<any> {
    const revertCustomizationCommand: ICommand = this.rxCommandFactoryService.forResourceType(
      'com.bmc.arsys.rx.application.association.command.RevertAssociationDefinitionCommand'
    );

    return revertCustomizationCommand.execute({ associationDefinitionName });
  }

  rename(oldAssociationDefinitionName: string, newAssociationDefinitionName: string): Observable<any> {
    const renameCommand: ICommand = this.rxCommandFactoryService.forResourceType(
      'com.bmc.arsys.rx.application.association.command.RenameAssociationDefinitionCommand'
    );

    return renameCommand.execute({
      name: oldAssociationDefinitionName,
      newName: newAssociationDefinitionName
    });
  }

  private getUrl(associationDefinitionName?: string): string {
    return associationDefinitionName
      ? `/api/rx/application/association/associationdefinition/${encodeURIComponent(associationDefinitionName)}`
      : `/api/rx/application/association/associationdefinition`;
  }
}
