import { Injectable, Injector } from '@angular/core';
import { DataPage, IDataPageRequestConfiguration, IDataPageResult, RxLogService } from '@helix/platform/shared/api';
import { Observable } from 'rxjs';

const associationInstanceDataPageQuery =
  'com.bmc.arsys.rx.application.association.datapage.AssociationInstanceDataPageQuery';

@Injectable({
  providedIn: 'root'
})
export class RxAssociationInstanceDataPageService extends DataPage {
  constructor(private injector: Injector, private rxLogService: RxLogService) {
    super(injector, associationInstanceDataPageQuery);
  }

  get(dataPageRequestConfiguration: IDataPageRequestConfiguration = {}): Observable<IDataPageResult> {
    this.rxLogService.warning(
      'RxAssociationInstanceDataPageService: The get() method is deprecated. Use post() instead.'
    );

    return super.get(dataPageRequestConfiguration);
  }
}
